body {
  height: 100vh;
  padding-top: 50px;
  font-family: sans-serif;
  position: relative;
}

.theme {
  color: #0f0;
  background-color: #000;
  border: 3px solid #0f0;
}

.open-modal-btn {
  cursor: pointer;
  border-radius: 15px;
  outline: none;
  margin: 0 auto;
  padding: 10px 15px;
  font-size: 40px;
  display: block;
}

.modal-window {
  text-transform: uppercase;
  padding: 100px 90px;
  font-size: 70px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-btn {
  color: #0f0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  font-size: 25px;
  text-decoration: underline;
  position: absolute;
  top: 20px;
  right: 20px;
}

.overlay {
  background-color: rgba(0, 0, 0, .5);
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

